  /* Charts */
  
  .charts-container {
    display: grid;
    grid-column-gap: 4rem;
    -webkit-column-gap: 4rem;
            column-gap: 4rem;
    grid-row-gap: 2rem;
    row-gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
    margin-bottom: 20px;
  }
  
  .chart-item:nth-of-type(1) {
    grid-column: 1;
    grid-row: 1;
  }
  
  .chart-item:nth-of-type(2) {
    grid-column: 2/4;
    grid-row: 1;
  }
  
  .chart-item:nth-of-type(3) {
    grid-column: 1/3;
    grid-row: 2;
  }
  
  .chart-item:nth-of-type(4) {
    grid-column: 3;
    grid-row: 2;
  }
  
  .chart-item,
  .chart-item>div {
    height: 200px;
  }
  
  .chart-item p {
    font-weight: 600;
    font-size: 1.1rem;
  }
  
